import React from 'react';
import cl from './Feedback.module.scss'

const Feedback: React.FC = () => {
    return (
        <form className={cl.feedback}>
            <input type="text" placeholder=' ' className={cl.feedback__input} />
            <input type="text" placeholder=' ' className={cl.feedback__input} />
            <div className={cl.feedback__item}>
                <button className={cl.feedback__button}> </button>
                <p className={cl.feedback__text}>  ,    <a className={cl.feedback__link} href="#"> </a></p>
            </div>
        </form>
    );
};

export default Feedback;